.card {
    display: flex;
    flex-direction: column;
    background: var(--bg-card);
    border: 1px solid var(--border-card);
    border-radius: var(--radius-xl);
    padding: var(--space-5);
    cursor: pointer;
    transition: all var(--transition-normal);
    height: 100%;
}

.card:hover {
    background: var(--bg-card-hover);
    border-color: rgba(255, 255, 255, 0.15);
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.3);
}

/* Header */
.header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: var(--space-4);
}

.badges {
    display: flex;
    gap: var(--space-2);
}

.newBadge,
.hotBadge {
    padding: var(--space-1) var(--space-2);
    font-size: 0.6875rem;
    font-weight: 600;
    border-radius: var(--radius-sm);
}

.newBadge {
    background: rgba(34, 197, 94, 0.15);
    color: #22c55e;
}

.hotBadge {
    background: rgba(239, 68, 68, 0.15);
    color: #ef4444;
}

.saveButton {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.05);
    border: none;
    border-radius: 50%;
    color: var(--text-muted);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.saveButton:hover {
    background: rgba(255, 255, 255, 0.1);
    color: var(--text-primary);
}

.saveButton.saved {
    color: #ef4444;
    background: rgba(239, 68, 68, 0.15);
}

/* Content */
.content {
    flex: 1;
    margin-bottom: var(--space-4);
}

.category {
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.title {
    font-size: 1.0625rem;
    font-weight: 700;
    color: var(--text-primary);
    margin: var(--space-2) 0;
    line-height: 1.4;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.organizer {
    font-size: 0.8125rem;
    color: var(--text-muted);
    margin: 0;
}

/* Tags */
.tags {
    display: flex;
    flex-wrap: wrap;
    gap: var(--space-2);
    margin-bottom: var(--space-4);
}

.difficultyTag,
.tag {
    padding: var(--space-1) var(--space-2);
    font-size: 0.6875rem;
    font-weight: 500;
    border-radius: var(--radius-sm);
}

.tag {
    background: rgba(255, 255, 255, 0.05);
    color: var(--text-muted);
}

/* Footer */
.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: var(--space-4);
    border-top: 1px solid var(--border-subtle);
}

.meta {
    display: flex;
    flex-direction: column;
    gap: var(--space-1);
}

.prize {
    font-size: 0.8125rem;
    font-weight: 600;
    color: var(--accent-orange);
}

.deadline {
    display: flex;
    align-items: center;
    gap: var(--space-1);
    font-size: 0.75rem;
    color: var(--text-muted);
}

.rating {
    display: flex;
    align-items: center;
    gap: var(--space-1);
    font-size: 0.875rem;
    font-weight: 600;
    color: #fbbf24;
}